ity transform margin-left;\n  \n  &#{$OPEN} {\n    // overflow: hidden;\n    // box-shadow: 0 0 3em 0 rgba(black,.1);\n    // box-shadow: 1.5em -1.5em 3em -1em rgba(black,.1);\n  }\n  &:not(#{$OPEN}) {\n    transform: translateX(-100%);\n    margin-left: -$sidebar-width;\n  }\n\n  @at-root .UiBox:last-child {\n    #{$B} + & {\n      opacity: 1;\n      transition: opacity .19s .19s ease-out;\n    }\n    #{$B}#{$OPEN} + & {\n      @include Break( max-width Breaks(3) ){\n        opacity: .5;\n        mix-blend-mode: multiply;\n        > * { pointer-events: none !important }\n      }\n    }\n  }\n\n  &:after {\n    $matte: Color(light);\n    content: \" \";\n    z-index: 2;\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: -1px;\n    left: 0;\n    pointer-events: none;\n    background: linear-gradient(\n      to top,\n      $matte 3px,\n      rgba($matte,.3) 2em,\n      rgba($matte,0) 3em\n      );\n    opacity: .9;\n  }\n  &--ribbon {\n    // content: \"Filters\";\n    cursor: pointer;\n    appearance: none;\n    user-select: none;\n\n    z-index: 9;\n    position: absolute;\n    left: 100%;\n    top: 0;\n    \n    @extend %rack;\n\n    height: $ribbon-height;\n    min-width: 5.5em;\n    margin-left: $sidebar-width;\n    padding: .1em nth($sidebar-pad,2);\n\n    font: inherit;\n    font-size: .9em;\n    font-weight: 300;\n    text-transform: uppercase;\n    letter-spacing: .1em;\n    color: Color(light);\n    border-width: 0 !important;\n    outline: none;\n    background: Color(theme);\n    box-shadow: .5em .25em 2em -.25em rgba(Color(dark),.3);\n\n    transition: inherit;\n\n    #{$B}#{$OPEN} & {\n      // transition: .38s 0s ease-out;\n      min-width: 100%;\n      padding-left: nth($sidebar-pad,2);\n      padding-right: nth($sidebar-pad,2);\n      margin-left: -$sidebar-width;\n      color: Color(light);\n      background: Color(slate);\n      box-shadow: 2px 0 0 0 Color(dark);\n      box-shadow: 0 0 0 0 Color(dark);\n    }\n\n    & .UiIcon {\n      position: absolute;\n      right: nth($sidebar-pad,2);\n      margin-right: -5px;\n      opacity: 0;\n      transition: inherit;\n    }\n    #{$B}#{$OPEN} & .UiIcon {\n      opacity: .5;\n      transition-delay: 0s;\n    }\n    #{$B}#{$OPEN} &:hover .UiIcon {\n      transition-delay: .38s;\n      &:hover { transition-delay: .0s !important }\n    }\n    // #{$B}#{$OPEN} .UiIcon { filter: invert(.75) }\n    \n  }\n  &--header {\n    @extend %rack;\n    .UiButton {\n      font-size: 0.7em;\n      margin-top: -0.25 * nth($sidebar-pad,2);\n      margin-right: -0.25 * nth($sidebar-pad,2);\n    }\n  }\n  &--inner {\n    z-index: 1;\n    position: relative;\n    width: 100%;\n    max-height: 100%;\n    max-height: calc( 100% - #{$ribbon-height});\n    overflow: scroll;\n    -webkit-overflow-scrolling: touch;\n    margin-top: $ribbon-height;\n    padding: 0 nth($sidebar-pad,2);\n    background: transparent;\n    background: Color(light);\n    border-width: 0;\n\n    box-shadow: 1.5em -2.5em 3em -1.25em rgba(Color(dark),.08);\n    // box-shadow: 0 0 3em 0 rgba(black,.1);\n\n    >:first-child { margin-top:    2rem }\n    >:last-child  { margin-bottom: 2rem }\n  }\n  @include Break( max-width Breaks(3) ){\n    & {\n      top: 4rem;\n      // margin-top: 6.5rem;\n      margin-top: 2rem;\n      max-height: calc(100vh - 75px); // TODO: set global $footerHeight to 75px\n      box-shadow: 1em 0 3em -1em rgba(black,.15);\n    }\n    &#{$OPEN} {\n      overflow: hidden;\n      margin-right: -$sidebar-width;\n    }\n    &--inner {\n      min-height: 100%;\n      >:last-child { margin-bottom: 3rem }\n    }\n    + :last-child { padding-top: 12rem }\n    @at-root #WineFiltersWrap > .UiPanel { flex-flow: nowrap row }\n    .UiHeading { text-align: left !important }\n  }\n}\n","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-alt:   #D21034; // or: #CB3E3D\n\n$colors:(\n  dark:    $c-black, // #0E0E0E\n  slate:   lighten($c-black,15%), // #323232,\n  silver:  lighten($c-black,67%), // #B8B8B8\n  cream:   lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: #BFB4A0,\n  light:   mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:   $c-matte, // #F0F0F0\n  theme:   $c-theme, // #BA9454\n  alt:     $c-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}"],"sourceRoot":""}]);

